import React ,{useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import {base_url, $http} from "config";
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

import {useDispatch} from 'react-redux'
import {userSignOut} from 'actions/Auth';
import {useHistory} from 'react-router-dom';

const DashBoard = (props) => {
    //const customer_id = props.location.state.id;
    const {width} = useSelector(({settings}) => settings);
    const [loader, setLoader] = useState(false);
    const [Lives, setLive] = useState(0);
    const [Archives, setArchive] = useState(0);
    const [Customers, setCustomer] = useState(0);
    const [Messages, setMessage] = useState(0);
    const [Tasks, setTask] = useState(0);
    const [Products, setProduct] = useState(0);
    const [Users, setUser] = useState(0);
    const [Sites, setSite] = useState(0);
    const [Rooms, setRoom] = useState(0);
    useEffect(() => {
        Init_data();
    }, []);
    const Init_data = () => {
        
        $http.get(base_url + "dashboard")
        .then((response) => {
            console.log(response.data);
            setLive(response.data.lives);
            setCustomer(response.data.customers);
            setTask(response.data.tasks);
            setProduct(response.data.products);
            setUser(response.data.users);
            setArchive(response.data.archives);
            setSite(response.data.sites);
            setRoom(response.data.rooms);
            setMessage(response.data.messages);
            setLoader(true);
        });
        
    }
    const dispatch = useDispatch();
    const History = useHistory();
    if (!localStorage.getItem("token"))
    {
        dispatch(userSignOut());
        History.push('/');
    }
    return (
        loader ? (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.dashboard"/>}/>
            <div className="row">
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="sidebar.sitesurvey.live"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Lives}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="mailNotification.title"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Messages}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="sidebar.taskmanager.mytasks"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Tasks}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="sidebar.sitesurvey.archived"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Archives}</h1>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="todo"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>0</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="todo"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>0</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.comments"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>0</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.customers"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Customers}</h1>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.sites"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Sites}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.rooms"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Rooms}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.products"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Products}</h1>
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-sm-6 col-12">
                    <div className="card jr-card-intra shadow text-center">
                        <div className="card-header py-3 align-items-center">
                            <h3 className="mb-0"><IntlMessages id="dashboard.users"/></h3>
                        </div>
                        <div className="stack-order py-4 px-2">
                            <h1 style={{fontSize: 80}}>{Users}</h1>
                        </div>
                    </div>
                </div>
            </div>
        </div>
                    
        ):(
            <div className="loader-view"
                    style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                <CircularProgress/>
            </div>                   
        )
    );

}

export default DashBoard;